#listIndex ul { 
  list-style-type: none; 
  margin: 0 0 10px 0;
  padding: 0;
  text-align:center;
}


#listIndex ul li { 
  font-weight:bold;
  display: inline;
  padding: .2em 1em;
}


@media(max-width:767px){
    #firstPageLogin{ }

    #firstTitle span:nth-child(1){ font-size:2em; font-weight:bold;}
    #firstTitle span:nth-child(2){ font-size:2em;}

}
@media(min-width:768px){
    #firstPageLogin{ width:300px; margin:0 auto; }

    #firstTitle span:nth-child(1){ font-size:3em; font-weight:bold;}
    #firstTitle span:nth-child(2){ font-size:3em;}
    #listIndex ul li {  font-size:1.3em; }
}
@media(min-width:992px){
    #firstPageLogin{ width:400px; margin:0 auto; }

    #firstTitle span:nth-child(1){ font-size:3.5em; font-weight:bold;}
    #firstTitle span:nth-child(2){ font-size:4em;}
    #listIndex ul li {  font-size:1.7em; }

}
@media(min-width:1200px){
    #firstPageLogin{ width:500px; margin:0 auto; }
    #listIndex ul li {  font-size:2em; }
}

